.footer__copyright {
    font-size: 18px;
    line-height: 22px;
    font-weight: normal;
    color: #545454;
    margin: 45px 0 0;
}

@media screen and (max-width: 900px) {
    .footer__copyright {
        padding-left: 19px;
    }
}

@media screen and (max-width: 568px) {
    .footer__copyright {
        font-size: 14px;
        line-height: 17px;
    }
}
