.gallery {
	max-width: 880px;
	margin: 46.25px auto 68px;
	display: grid;
	grid-template-columns: repeat(auto-fit, 282px);
	grid-gap: 20px 17px;
	justify-content: center;
	list-style: none;
	padding: 0;
}

@media screen and (max-width: 568px) {
	.gallery {
		margin: 36px auto 50px;
	}
}
